@font-face {
  font-family: 'Ubuntu';
  src: url($fonts + 'Ubuntu-Regular/Ubuntu-Regular.woff2') format('woff2'),
    url($fonts + 'Ubuntu-Regular/Ubuntu-Regular.woff') format('woff'),
    url($fonts + 'Ubuntu-Regular/Ubuntu-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'icomoon';
  src: url($fonts + 'icomoon/icomoon.woff') format('woff'),
    url($fonts + 'icomoon/icomoon.ttf') format('truetype'),
    url($fonts + 'icomoon/icomoon.svg#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1.2;
  vertical-align: middle;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-arrow-left:before {
  content: "\e900";
}
.icon-arrow-right:before {
  content: "\e901";
}
.icon-calculator:before {
  content: "\e902";
}