.l-grid {
  font-size: 0;
  margin: 0 -15px;
  .l-col-6 {
    display: inline-block;
    vertical-align: top;
    width: 50%;
    padding: 0 15px;
    font-size: 1rem;
    @include r(479) {
      width: 100%;
      text-align: center;
    }
  }
}

.b-page {
  display: table;
  width: 100%;
  height: 100vh;
  background: url($img + 'main-bg.jpg') no-repeat center top;
  background-size: cover;
  @include r(767) {
    background: url($img + 'main-bg-mobile.jpg') no-repeat center top;
    background-size: cover;
  }
  &__inner {
    display: table-cell;
    min-height: 840px;
    padding-top: 150px;
    padding-bottom: 150px;
    text-align: center;
    vertical-align: middle;
    @media (max-height: 1000px) {
      padding-top: 110px;
      padding-bottom: 50px;
    }
    @include r(959) {
      padding-bottom: 100px;
    }
    @include r(479) {
      padding-top: 170px;
    }
  }
}//b-page

.b-offer {
  max-width: 1020px;
  margin: 0 auto 90px;
  transform: translateX(25px);
  @include r(1100) {
    transform: none;
  }
  @include r(1000) {
    margin-bottom: 50px;
  }
  @include r(666) {
    display: none;
  }
  &__item {
    padding: 10px 0 10px 50px;
    display: inline-block;
    max-width: 280px;
    position: relative;
    text-align: left;
    @include r(959) {
      max-width: 230px;
    }
    &::after {
      content: "";
      position: absolute;
      left: 27px;
      top: 0;
      width: 93px;
      height: 95px;
      background: url($img + 'icons/offer-border.svg') no-repeat;
    }
  }
  &__number {
    opacity: 0.1;
    color: #fff;
    font-size: 48px;
    font-weight: 700;
    line-height: 1;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
  }
  &__text {
    color: #fff;
    font-size: 22px;
    font-weight: 400;
    line-height: 24px;
    @include r(959) {
      font-size: 18px;
      line-height: 1.2;
      br {
        display: none;
      }
    }
  }
}//b-offer

.b-steps {
  margin: 10px 10px 30px;
  cursor: default;
  text-align: left;
  user-select: none;
  transition: all 0.5s;
  @include r(1200) {
    text-align: center;
  }
  @include r(479) {
    display: none;
  }
  & > div {
    display: inline-block;
    text-align: center;
    margin-right: 45px;
    padding-top: 7px;
    width: 42px;
    height: 42px;
    color: rgba(#fff, 0.2);
    font-size: 21px;
    font-weight: 400;
    text-decoration: none;
    position: relative;
    transition: all 0.3s;
    z-index: 1;
    @include r(666) {
      margin-right: 20px;
    }
    &.is-active {
      color: #fff;
      pointer-events: none;
      &::before {
        opacity: 1;
        background-color: #ff7403;
      }
    }
    &:hover {
      cursor: pointer;
      color: #fff;
    }
    &:last-of-type {
      &::after {
        content: none;
      }
    }
    &::after {
      content: "";
      position: absolute;
      width: 15px;
      height: 1px;
      opacity: 0.15;
      background-color: #fff;
      top: 50%;
      margin-top: -1px;
      right: -34px;
      @include r(666) {
        content: none;
      }
    }
    &::before {
      content: "";
      width: 42px;
      height: 42px;
      position: absolute;
      background-color: #fff;
      left: 0;
      top: 0;
      opacity: 0.2;
      transform: rotate(45deg);
      z-index: -1;
      transform-origin: center center;
      transition: all 0.3s;
    }
  }
}//b-steps

.b-screen-1 {
  &.is-hiding {
    transform: translateX(-200%);
    transition: all 3s;
  }
}

.b-screen-2 {
  display: none;
  transform: scale(0.3);
  opacity: 0;
  transition: all 1s;
  z-index: 10;
  &.is-visible {
    transform: scale(1);
    opacity: 1;
  }
  .l.is-centered {
    //display: table;
    .b-screen-2__right {
      width: 0;
      height: 0;
      transition: all 0.5s;
    }
    .b-screen-2__content {
      justify-content: center;
    }
    .b-steps {
      text-align: center;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    position: relative;
    transition: all 0.5s;
    z-index: 5;
    @include r(1200) {
      justify-content: center;
    }
    @include r(767) {
      display: block;
    }
  }
  &__right {
    //float: right;
    width: 370px;
    overflow: hidden;
    transition: none;
    @include r(1200) {
      width: auto;
    }
    @include r(767) {
      //display: none;
    }
    .b-info {
      @media (max-height: 1000px) {
        margin-bottom: 15px;
      }
      @include r(1200) {
        display: inline-block;
      }
      &:first-of-type {
        @include r(1200) {
          display: none;
        }
      }
    }
  }
  &__left {
    //float: left;
    width: 770px;
    @include r(959) {
      max-width: 770px;
      width: auto;
    }
  }
}

.js-info {
  &__close {
    width: 44px;
    height: 44px;
    line-height: 44px;
    position: absolute;
    right: 0;
    top: 0;
    text-decoration: none;
    text-align: center;
    opacity: 0.65;
    padding: 0 0 18px 10px;
    color: #fff;
    font-style: normal;
    font-size: 28px;
    font-family: Arial, Baskerville, monospace;
    color: #333;
    transition: all 0.3s;
    user-select: none;
    &:hover {
      opacity: 1;
      cursor: pointer;
    }
  }
}

.b-info {
  max-width: 370px;
  padding: 20px 10px;
  margin-bottom: 30px;
  background-color: rgba(#000, 0.55);
  box-shadow: 0 10px 18px 2px #0e141e;
  text-align: center;
  color: #fff;
  z-index: 5;
  @include r(479) {
    padding-top: 35px;
    max-width: 290px;
  }
  &.js-info {
    @include r(1200) {
      position: absolute;
      left: 0;
      top: -65px;
      z-index: 5;
      transform: translateX(-110%);
      opacity: 0;
      background-color: #000;
      transition: all 0.5s;
      &.is-open {
        opacity: 1;
        transform: translateX(0)
      }
    }
    @include r(479) {
      max-width: 290px;
    }
  }
  &:last-of-type {
    margin-bottom: 0;
    
  }
  &__title {
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 26px;
    color: #fff;
    &.mb-small {
      margin-bottom: 10px;
    }
    &.size-small {
      margin-bottom: 15px;
      font-size: 22px;
      line-height: 1;
    }
    span {
      display: block;
      color: #ff7403;
      font-weight: 700;
    }
  }
  &__discount {
    .title {
      color: #fff;
      font-size: 28px;
      font-weight: 400;
    }
    .price {
      display: block;
      color: #ff7403;
      font-size: 50px;
      font-weight: 700;
    }
  }
}//b-info

.b-callback {
  padding: 0 50px;
  @include r(479) {
    padding: 0 20px;
  }
  &__text {
    margin-bottom: 10px;
    color: rgba(#fff, 0.4);
    font-size: 13px;
    font-weight: 400;
    line-height: 16px;
  }
}//b-callback

.b-calc {
  text-align: left;
  background-color: #fff;
  padding-bottom: 100px;
  position: relative;
  transition: all 0.5s;
  min-height: 567px;
  box-shadow: 0 10px 18px 2px #0e141e;
  @include r(1200) {
    height: 567px;
    min-height: none;
  }
  @media (max-height: 900px) {
    padding-bottom: 80px;
  }
  @include r(767) {
    height: auto;
  }
  @include r(479) {
    padding-bottom: 0;
  }
  &__title {
    margin-bottom: 15px;
    padding: 23px 65px 17px;
    border-bottom: 1px solid rgba(#34495e, 0.15);
    color: #000;
    font-size: 30px;
    @include r(666) {
      padding: 23px 20px 17px;
      font-size: 26px;
    }
    @include r(479) {
      font-size: 16px;
    }
  }

  &__content {
    padding: 0 65px;
    @include r(666) {
      padding: 0 20px;
    }
  }

  &__gate {
    display: inline-block;
    vertical-align: middle;
    max-width: 90%;
    height: auto;
    @include r(767) {
      vertical-align: bottom;
    }
  }

  &__parameters {
    text-align: left;
    .title {
      margin-bottom: 13px;
      color: #ec7210;
      font-size: 30px;
      font-weight: 700;
      @include r(479) {
        font-size: 24px;
      }
    }
    ul {
      margin: 0 0 20px;
      padding: 0;
      list-style: none;
      counter-reset: list;
      color: #404040;
      //font-size: 22px;
      font-size: 18px;
      font-weight: 400;
      //line-height: 24px;
      @media (max-height: 900px) {
        font-size: 15px;
      }
      li {
        //margin-bottom: 10px;
        margin-bottom: 5px;
        padding-left: 25px;
        counter-increment: list;
        position: relative;
        & > div {
          margin-bottom: 10px;
        }
        &::before {
          content: counter(list) ".";
          position: absolute;
          left: 0;
          top: 0;
          color: #ff7403;
          font-weight: 700;
        }
      }
    }
  }//b-calc__parameters

  &__bottom {
    padding: 23px 65px 17px;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100px;
    @media (max-height: 900px) {
      height: 80px;
    }
    @include r(666) {
      padding: 23px 20px 17px;
    }
    @include r(479) {
      height: auto;
      display: block;
      position: static;
      text-align: center;
    }
  }
  &__summary {
    color: #000;
    font-size: 30px;
    @include r(767) {
      font-size: 24px;
    }

    .cross {
      text-decoration: line-through;
    }
  }
  &__discount {
    display: table;
    margin-left: 80px;
    margin-bottom: 5px;
    padding: 0 5px;
    color: #fff;
    font-size: 20px;
    font-weight: 400;
    line-height: 28px;
    background-color: #ea2713;
    @include r(479) {
      margin: 0 auto 5px;
    }
    &-text {
      margin-left: 80px;
      color: #4e4e4e;
      font-size: 14px;
      font-style: italic;
      letter-spacing: -0.025em;
      @include r(479) {
        margin-left: 0;
        text-align: center;
      }
    }
  }
  &__newPrice {
    margin-bottom: 15px;
    color: #ea2713;
    margin-right: 35px;
    font-size: 42px;
    font-weight: 700;
    text-align: right;
  }
  &__step {
    visibility: hidden;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    //bottom: 100px;
    transform: scale(0.3);
    opacity: 0;
    transition: all 0.8s;
    z-index: 1;
    @include r(767) {
      display: none;
      position: static;
      visibility: visible;
      transform: none;
      transition: none;
    }
    &.is-visible {
      visibility: visible;
      transform: scale(1);
      opacity: 1;
      @include r(767) {
        display: block;
      }
    }
    &.item-last {
      bottom: 0;
      position: static;
      margin-bottom: -75px;
      padding-bottom: 10px;
      @include r(767) {
        margin-bottom: 0;
        .l-col-6 {
          width: auto;
          display: table;
          margin: 0 auto;
          text-align: left;
        }
      }
    }
    &.item-1 {
      @include r(767) {
        .b-radio:first-of-type {
          margin-top: 40px;
        }
      }
    }
  }
  &__nav {
    font-size: 0;
    position: relative;
    &-error {
      position: absolute;
      bottom: 110%;
      right: 0;
      color: #ea2713;
      font-size: 16px;
      opacity: 0;
      transition: all 0.3s;
      background-color: #fff;
      z-index: 5;
      &.is-visible {
        opacity: 1;

      }
    }
  }
  &__next, &__prev {
    display: inline-block;
    padding: 12px 30px;
    color: #000;
    font-size: 24px;
    font-weight: 400;
    transition: all 0.3s;
    background-color: #f0f0f0;
    user-select: none;
    @include r(666) {
      font-size: 18px;
      padding: 12px 20px;
      &::after {
        font-size: 14px;
      }
    }
    &::after, &::before {
      font-size: 16px;
      vertical-align: 0px;
      font-family: 'icomoon';
      
    }
    &:hover {
      cursor: pointer;
      background-color: #ea2713;
      color: #fff;
    }
  }
  &__prev {
    padding-left: 20px;
    &::before {
      content: "\e900";
      padding-right: 8px;
      vertical-align: 1px;
      @include r(666) {
        vertical-align: -1px;
      }
    }
  }
  &__next {
    padding-right: 20px;
    &::after {
      content: "\e901";
      padding-left: 8px;
    }
  }
}//b-calc

.b-callback2 {
  width: 310px;
  padding: 20px;
  margin: 0 auto 15px;
  background-color: #fff;
  box-shadow: 0 3px 8.2px 0.8px rgba(0, 0, 0, 0.2);
  @include r(479) {
    width: auto;
    max-width: 100%;
  }
  &__title {
    margin-bottom: 6px;
    color: #000;
    font-size: 24px;
    font-weight: 400;
    line-height: 1;
  }
  &__text {
    list-style: decimal inside;
    margin: 0 0 10px;
    padding: 0;
    color: #7a7a7a;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
  }
  &__outerText {
    opacity: 0.5;
    color: #000;
    font-size: 14px;
    font-weight: 400;
    text-align: center;
  }
  .b-input2:last-of-type {
    margin-bottom: 20px;
  }
}//b-callback2

.b-rangeSlider {
  //padding: 12px;
  &.second {
    display: inline-block;
    vertical-align: middle;
    @include r(767) {
      vertical-align: middle;
    }
  }
  &.type-horizontal {
    max-width: 100%;
    width: 400px;
    height: 60px;
    @include r(479) {
      width: 240px;
    }
    .b-rangeSlider__slider {
      width: 290px;
      @include r(479) {
        width: 240px;
      }
    }
  }
  &.type-vertical {
    width: 230px;
    height: 190px;
    position: relative;
    @include r(767) {
      width: 20px;
    }
    .b-counter {
      margin-left: 20px;
      @include r(767) {
        position: absolute;
        top: 100%;
        right: 0;
        margin-left: 0;
        white-space: nowrap;
      }
    }
  }
  .b-counter {
    display: inline-block;
    vertical-align: middle;
    color: rgba(#000, 0.5);
    font-size: 24px;
    font-weight: 400;
    strong {
      color: #000;
      font-weight: 400;
    }
    input {
      margin-left: 10px;
      color: inherit;
      padding: 0;
      padding-right: 5px;
      width: 60px;
      border: none;
      background: transparent;
      text-align: right;
    }
  }
  &__slider {
    display: inline-block;
    vertical-align: middle;
  }
}//b-rangeSlider

.b-questions {
  display: none;
  margin-bottom: 10px;
  color: #fff;
  text-align: left;
  align-items: center;
  justify-content: center;
  @include r(1200) {
    display: flex;
  }
  .btn {
    flex-shrink: 0;
    margin-right: 20px;
  }
}//b-questions

.b-radioLevel {
  visibility: hidden;
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  background-color: #fff;
  z-index: 5;
  opacity: 0;
  transition: all 0.4s;
  &.is-visible {
    visibility: visible;
    opacity: 1;
  }
}

#callback-form {
  margin: 0 auto;
  background-color: #000;
}

#popup-thanks {
  padding-top: 45px;
  background-color: #000;
  margin: 0 auto;
  position: relative;
}