.b-flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.b-header {
  padding: 25px 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(#000, 0.5);
  box-shadow: 0 6px 18.8px 1.2px #0e141e;
  @media (max-height: 1000px) {
    padding: 5px 0;
  }
  .b-flex {
    @include r(479) {
      display: block;
    }
  }
  &__text {
    padding-top: 10px;
    color: #fff;
    font-size: 20px;
    font-weight: 400;
    line-height: 22px;
    text-align: center;
    @include r(666) {
      font-size: 16px;
      line-height: 1.2;
      br {
        display: none;
      }
    }
  }
  &__left, &__right {
    width: 250px;
    max-width: 250px;
    @include r(959) {
      width: auto;
    }
  }
  &__left {
    @include r(767) {
      display: none;
    }
  }
  &__right {
    text-align: right;
    flex-shrink: 0;
    @include r(479) {
      display: table;
      margin: 10px auto;
      text-align: center;
    }
  }
}//b-header
