.b-footer {
  padding: 30px 0 40px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(#000, 0.2);
  color: rgba(#fff, 0.3);
  font-size: 18px;
  @media (max-height: 1000px) {
    padding: 10px 0;
  }
  .b-flex {
    @include r(666) {
      display: block;
      text-align: center;
    }
  }
  &__left, &__right {
    width: 250px;
    @include r(767) {
      width: auto;
    }
    @include r(666) {
      text-align: center;
    }
  }
  &__right {
    text-align: right;
    @include r(666) {
      margin-top: 5px;
      text-align: center;
    }
  }
  .phone {
    display: inline-block;
    padding-left: 15px;
    color: rgba(#fff, 0.3);
    text-decoration: none;
    position: relative;
    &::before {
      content: "";
      width: 10px;
      height: 10px;
      border: 1px solid #656a76;
      position: absolute;
      left: 0;
      top: 50%;
      margin-top: -5px;
    }
  }
}//b-footer