.rangeslider,
.rangeslider__fill {
  display: block;
  border-radius: 3px;
}
.rangeslider {
  background: #ffcea7;
  position: relative;
}
.rangeslider--horizontal {
  height: 5px;
  width: 100%;
}

.rangeslider--vertical {
  width: 5px;
  min-height: 190px;
  max-height: 100%;
}
/*right-to-left*/
.rangeslider__rtl {
    direction: rtl;
}
/*top-to-bottom*/
.rangeslider__ttb {
    top: 0;
    bottom: initial;
}
.rangeslider--disabled {
    opacity: 0.4;
}

.rangeslider__fill {
    background: #ff7403;
    position: absolute;
}

.rangeslider--horizontal .rangeslider__fill {
    top: 0;
    height: 100%;
}

.rangeslider--vertical .rangeslider__fill {
    bottom: 0;
    width: 100%;
}

.rangeslider__handle {
  cursor: pointer;
  display: inline-block;
  width: 18px;
  height: 18px;
  background-color: #fff;
  backface-visibility: hidden;
  box-shadow: 3px 3px 8px 0.8px rgba(0, 0, 0, 0.2);
  transform: rotate(45deg);
  position: absolute;
  transition: box-shadow 0.3s;
}

.rangeslider__handle:after {
  content: "";
  position: absolute;
  left: 5px;
  top: 50%;
  margin-top: -4px;
  width: 8px;
  height: 8px;
  background-color: #ea2713;
  z-index: 1;
}

.rangeslider__handle:active,
.rangeslider--active .rangeslider__handle {
  box-shadow: 3px 3px 8px 0.8px rgba(0, 0, 0, 0.25);
}

.rangeslider--horizontal .rangeslider__handle {
  top: -7px;
  touch-action: pan-y;
  -ms-touch-action: pan-y;
}

.rangeslider--vertical .rangeslider__handle {
    left: -7px;
    touch-action: pan-x;
    -ms-touch-action: pan-x;
}