.b-phone {
  display: inline-block;
  margin-bottom: 5px;
  padding-left: 15px;
  color: #fff;
  font-size: 25px;
  font-weight: 400;
  text-decoration: none;
  position: relative;
  &::before {
    content: "";
    width: 11px;
    height: 11px;
    border: 1px solid #ff7403;
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: -5px;
  }
}//b-phone

input:not([type="submit"]), textarea, select {
  padding: 18px 20px 17px;
  background-color: #fff;
  border: 1px solid #c0c0c0;
  outline: none;
  transition: all 0.4s;
  font-family: Lato, sans-serif;
  line-height: 1.2;
  @include placeholder {
    line-height: 1.2;
    opacity: 1;
    color: rgba(#fff, 0.3);
    transition: opacity 0.5s;
    font-family: Arial, Helvetica, sans-serif;
  }
  &:focus {
    @include placeholder {
      opacity: 0;
    }
  }
}

textarea {
  resize: none;
  height: 100px;
}

.b-input {
  display: block;
  font-weight: 400;
  margin: 0 auto 15px;
  position: relative;
  z-index: 1;
  font-size: 18px;
  color: #fff;
  input:not([type="submit"]), textarea, select {
    color: #fff;
    padding: 2px 10px;
    width: 100%;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid rgba(#fff, 0.3);
    text-align: center;
    &:focus {
      border-color: #fff;
    }
  }
  &__field {
    position: relative;
    &.is-focus {
      &::before {
        background-color: #fff;
      }
    }
    &::before {
      content: "";
      width: 5px;
      height: 5px;
      background-color: rgba(#fff, 0.3);
      position: absolute;
      left: 10px;
      top: 50%;
      margin-top: -3px;
      border-radius: 50%;
    }
  }
  &__label {
    display: block;
    margin-bottom: 10px;
    color: #3d3d3d;
    font-size: 14px;
    font-weight: 400;
  }
  &__error {
    left: 0;
    margin-top: 5px;
    color: #f2736d;
    font-size: 14px;
    font-weight: 300;
    text-align: left;
  }
}//b-input

