.b-doubleRadio {
  position: relative;
  &__hidden {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 2;
    .b-radio {
      background-color: #fff;
      width: 45%;
      box-shadow: 0 3px 8.2px 0.8px rgba(0, 0, 0, 0.2);
    }
  }
}

.b-radio {
  //display: table;
  display: block;
  text-align: left;
  margin: 0;
  margin-left: 2px;
  vertical-align: middle;
  position: relative;
  cursor: pointer;
  user-select: none;
  //text-align: center;
  &__wrap {
    position: relative;
  }
  &.with-img {
    display: inline-block;
    margin-bottom: 25px;
    img {
      max-width: 100%;
      height: auto;
      box-shadow: 0 3px 8.2px 0.8px rgba(0, 0, 0, 0.2);
    }
    & > span {
      display: table;
      padding: 5px 10px 5px 45px;
      margin: -22px auto 0;
      background-color: #fff;
      opacity: 1;
      line-height: 1.2;
      box-shadow: 0 3px 8.2px 0.8px rgba(0, 0, 0, 0.2);
      color: #a1a1a1;
      @include r(666) {
        font-size: 12px;
        line-height: 1.2;
      }
      &::after {
        left: 10px;
        opacity: 0.4;
      }
      &::before {
        left: 15px;
        opacity: 0.4;
      }

    }
  }
  input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    &:checked + span {
      color: #000;
      &::after, &::before {
        opacity: 1;
      }
    }
  }
  & > span {
    display: block;
    padding-left: 26px;
    font-size: 20px;
    font-weight: 400;
    line-height: 32px;
    color: rgba(#000, 0.4);
    //opacity: 0.4;
    transition: all 0.3s;
    position: relative;
    @include r(479) {
      font-size: 16px;
    }
    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      margin-top: -9px;
      width: 18px;
      height: 18px;
      opacity: 0.4;
      background-color: #fff;
      backface-visibility: hidden;
      box-shadow: 3px 3px 8px 0.8px rgba(0, 0, 0, 0.2);
      transform: rotate(45deg);
      transition: all 0.3s;
    }
    &::before {
      content: "";
      position: absolute;
      left: 5px;
      top: 50%;
      margin-top: -4px;
      width: 8px;
      height: 8px;
      opacity: 0.4;
      background-color: #ea2713;
      transform: rotate(45deg);
      z-index: 1;
      transition: all 0.3s;
    }
  }
}//b-radio

.b-logo {
  display: inline-block;
  color: #f8a10a;
  font-family: Ubuntu, sans-serif;
  font-size: 26px;
  font-weight: 400;
  line-height: 31px;
  text-decoration: none;
  span {
    margin-bottom: -8px;
    margin-left: -3px;
    display: block;
    color: #f7a10b;
    font-size: 75px;
    line-height: 1;
    text-transform: uppercase;
  }
}//b-logo

.b-title {
  margin-bottom: 35px;
  font-size: 52px;
  line-height: 1;
  color: #fff;
  font-weight: 400;
  @include r(1000) {
    font-size: 36px;
    margin-bottom: 20px;
  }
  @include r(767) {
    font-size: 32px;
  }
  @include r(479) {
    font-size: 24px;
  }
  span {
    color: #ff7403;
    font-weight: 700;
    }
}//b-title

.b-subtitle {
  margin-bottom: 40px;
  color: #fff;
  font-size: 38px;
  font-weight: 400;
  @include r(1000) {
    font-size: 28px;
    margin-bottom: 20px;
  }
  @include r(767) {
    display: none;
  }
}

.btn {
  display: inline-block;
  outline: none;
  text-decoration: none;
  color: #000;
  border: none;
  text-align: center;
  background-color: transparent;
  font-size: 20px;
  font-family: 'Arial', 'Helvetica', sans-serif;
  line-height: 1.2;
  font-weight: 700;
  transition: all 0.3s;
  z-index: 5;
  &--std {
    padding: 8px 35px 10px;
    color: #fff;
    font-size: 20px;
    font-weight: 400;
    background-color: #ec7210;
    box-shadow: 0 5px 0 rgba(#fff, 0.2);
    &:hover {
      background-color: #ff7200;
      box-shadow: none;
    }
  }//btn--std
  &--alt {
    padding: 20px 20px 25px 30px;
    color: #fff;
    font-size: 32px;
    font-weight: 700;
    background-color: #ea3113;
    box-shadow: 0 9px 0 rgba(#fff, 0.2);
    @include r(479) {
      padding-top: 15px;
      padding-bottom: 20px;
      font-size: 18px;
    }
    i {
      display: inline-block;
      margin-right: 12px;
      vertical-align: 0;
      font-size: 28px;
      @include r(479) {
        font-size: 20px;
        vertical-align: -2px;
      }
    }
    &:hover {
      background-color: darken(#ea3113, 5%);
      box-shadow: none;
    }
  }//btn--alt
  &--red {
    padding: 8px 35px 10px;
    color: #fff;
    width: 100%;
    font-size: 24px;
    font-weight: 400;
    background-color: #ea3113;
    box-shadow: 0 5px 0 rgba(#fff, 0.15);
    &:hover {
      background-color: darken(#ea3113, 5%);
      box-shadow: none;
    }
  }
}//btn